                    {`${severityValueRange[0]} - ${severityValueRange[1]}`}
                </div>
            </div>
        {/each}
        <div class="severity-row" title="severity-row">
            <div
                class="severity-color"
                style="background: {ColorHelper.color(severityLevels, severityLevels)}" />
